<template>
	<div id="home">
		<header class="hometop">
			<div class="search">
				<button class="schBtn"></button>
				<input type="text" placeholder="搜索最热品牌和商家"/>
			</div>		
		</header>
		<!--底部-->
		<myfooters></myfooters>
		
		<div class="bannerimg">
			<ppt></ppt>
		</div>
		
		<nav class="nav">
			<router-link :to="{name:'homelist',params:{title:'服饰'}}">服饰</router-link><router-link :to="{name:'homelist',params:{title:'箱包'}}">箱包</router-link><router-link :to="{name:'homelist',params:{title:'化妆品'}}">化妆品</router-link><router-link :to="{name:'homelist',params:{title:'数码'}}">数码</router-link><router-link :to="{name:'homelist',params:{title:'更多'}}">更多</router-link>
		</nav>
		<div class="indexList">
						<!--:to="{name:'goodsDetail',params:{id:item.id}}-->
			<router-link :to="{name:'homelist',params:{title:'时尚新品'}}" class="indexList-left">
					<img src="../assets/img/1.png"/>
			</router-link>
			<div class="indexList-right">
				<router-link :to="{name:'homelist',params:{title:'每日精选'}}">
					<img src="../assets/img/2.png"/>
				</router-link>
				<router-link :to="{name:'homelist',params:{title:'男士经典'}}" class="indexList-right1">
					<img src="../assets/img/3.png"/>
				</router-link>
				<router-link :to="{name:'homelist',params:{title:'高级配饰'}}" class="indexList-right2">
					<img src="../assets/img/4.png"/>
				</router-link>
			</div>
		</div>
			
			
		<div class="indexTli">
			<mt-navbar v-model="navBar">
			  <mt-tab-item id="1">最热</mt-tab-item>
			  <mt-tab-item id="2">低价</mt-tab-item>
			</mt-navbar>
			
			<!-- tab-container -->
			<mt-tab-container v-model="navBar">
			  <mt-tab-container-item id="1">
			    <div class="indexTli_list" >
					<router-link v-for="item in proList.hot" :key="item.id" :to="{name:'goodsDetail',params:{id:item.id}}" class="indexTli_info">
						<img :src="item.imgUrl" alt="" />
						<p>{{item.title}}</p>
						<span>{{item.price}}</span>
					</router-link>
					
				</div>
			  </mt-tab-container-item>
			  <mt-tab-container-item id="2">
			   	<div class="indexTli_list">
				    <router-link v-for="item in proList.lowPrice" :key="item.id" :to="{name:'goodsDetail',params:{id:item.id}}" class="indexTli_info">
						<img :src="item.imgUrl" alt="" />
						<p>{{item.title}}</p>
						<span>{{item.price}}</span>
					</router-link>
				</div>
			  </mt-tab-container-item>
			</mt-tab-container>
			
			<div class="idxjx">
				<div class="idxjx_top">
					<span></span><b>每日精选</b><span></span>
				</div>
				<div class="idxjx_list">
					
					<router-link :to="{name:'goodsDetail',params:{id:item.id}}" class="idxjx1" v-for="item in proList.dayGoods" :key="item.id">
						<div class="idxjx_photo1">
							<img :src="item.imgUrl"/>
						</div>
						<div class="idxjx_info1">
							<h3>{{item.title}}</h3>
							<p>{{item.description}}</p>
							<b>{{item.price}}</b>
							<span>{{item.style}}</span>
						</div>
					</router-link>
					
					
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
import ppt from '@/components/public/ppt'
export default {
  data(){
  	return{
  		proList:{},
  		navBar:'1'
  	}
  },
  components:{
  	ppt
  },
  created(){
  	this.$http.get('/home').then(res=>{
//		console.log(res);
  		this.proList= res.data.data;
  	})
  }
}

</script>

<style scoped>
	/*选项卡*/
	.mint-navbar a{
	    color: #A8A8A8;
	}
	.mint-navbar .mint-tab-item.is-selected {
	    border-bottom: 2px solid #D71E25;
	    color: #D71E25;
	    margin-bottom: -3px;
	}
#home{
	background: #F9F9F9;
	overflow: hidden;
	margin-bottom: 50px;
}
.hometop{
	width: 100%;
	height: 48px;
	padding:4px 0;
	background: #fff;
	overflow: hidden;
	}
.search{
	height:36px;
	width: 90%;
	background: #F3EADE;
	border-radius: 10px;
	margin: 2px auto;
	line-height: 36px;
	position: relative;
	}

.schBtn{
	width: 40px;
	height: 36px;
	margin-left: 20%;
	border: none;
	background:url(../assets/img/icons/search.png) no-repeat center 4px;
	float: left;
	}
.search input{
	float: left;
	padding-left: 5%;
	width:60%;
	height:36px;
	line-height: 36px;
	border:none;
	background-color:rgba(0,0,0,0);
	font-size:14px;
	color:rgba(0,0,0,.7);
	}	
.nav{
	background:#fff;
	height: 104px;
	clear: both;
	overflow: hidden;
	}
.nav a{
	width: 20%;
	display: inline-block;
	padding-top: 70px;
	text-align: center;
	background:url(../assets/img/icons/close.png) no-repeat center 20px;
	background-size: 48px;
	}

.nav a:nth-child(2) {
	background-image: url(../assets/img/icons/xb.png);
	}
.nav a:nth-child(3) {
	background-image: url(../assets/img/icons/hzp.png);
	}
.nav a:nth-child(4) {
	background-image: url(../assets/img/icons/sm.png);
	}
.nav a:nth-child(5) {
	background-image: url(../assets/img/icons/more.png);
	}
	.indexTli{
		margin-top: 8px;
	}
.indexList{
	clear: both;
	margin-top: 8px;
	width: 100%;
	overflow: hidden;
	background: #fff;
	overflow: hidden;
	}
.indexList-left,.indexList-right1,.indexList-right2{
	width: 50%;
	float: left;
	}
.indexList img{width: 100%;vertical-align: top;}
.indexList-right{
	float: left;
	width:50%;
	}
.indexTli_top{
	height: 48px;
	background: #fff;
	line-height: 48px;
	margin: 8px 0;
	}
.indexTli_top p{
	float: left;
	width: 50%;
	height: 48px;
	line-height: 48px;
	text-indent: 30px;
	color: #949494;
	}
.indexTli_top p:nth-child(1) span{
	text-indent: 0px;
	margin-right:30px;
	float: right;
	height: 35px;
	color:#333333;
	border-bottom: 2px solid #333333;
	}
.indexTli_list{
	margin-top: 8px;
	width: 100%;
	overflow: hidden;	
	}
.indexTli_list a{
	display: block;
	width: 49%;
	float: left;
	background: #fff;
	}
.indexTli_list a:nth-child(1){
	margin-right: 1%;
	}
.indexTli_list a img{
	width: 100%;
	}
.indexTli_list p,.indexTli_list span{
	display: block;
	width: 100%;
	height: 30px;
	line-height: 30px;
	text-align: center;
	}
.indexTli_list span{
	color: #fe2400;
	}
.idxjx_top span{
	display: inline-block;
	height: 2px;
	width: 30px;
	margin-bottom: 5px;
	background: #333333;
	}
.idxjx_top b{
	font-weight: normal;
	padding: 0 1em;
	}
.idxjx_top{
	height: 48px;
	background: #fff;
	line-height: 48px;
	margin-top: 8px;
	text-align: center;
	}
.idxjx1{
	display: block;
	width: 96%;
	margin: 8px 2%;
	overflow: hidden;
	background: #fff;
	padding: 20px 0 10px 4%;
}
.idxjx_photo1{
	height: 170px;
	overflow: hidden;
	width: 40%;
	float: left;
	margin-right: 20px;
	text-align: center;
	overflow: hidden;
}
.idxjx_photo1 img{
	width: 138px;
}
.idxjx_info1{
	width: 50%;
	float: left;
}
.idxjx_info1 h3{
	width: 100%;
	line-height: 28px;
	font-size:16px;
	font-weight: normal;
	text-align: justify;
	}
.idxjx_info1 p{
	margin: 10px 0;
	line-height: 35px;
	color: #9d9d9d;
}
.idxjx_info1 b{
	color:#fe2400;
	margin-right:10px;
	font-weight: normal;
}
.idxjx_info1 span{color: #9d9d9d;}


</style>


