import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)
export default new Vuex.Store({
	//state 状态
	//mutations 改变状态
	//getters 动态的给对象设置属性
	//actions 动作
	state:{
		name:'',
		tel:'',
		address:'',
		shopCarList:[],
		byList:[]
	},
	mutations:{
		
		changeList(state,val){
			state.shopCarList.push(val)
		},
		
		changeName(state,val){
			state.name = val;
		},
		changeByList(state,val){
			state.byList.push(val)
		},
		changeTel(state,val){
			state.tel = val;
		},
		update(state,val){
			state.shopCarList = val;
			console.log(state.shopCarList)
		}
	}
})



