<template>
	<div id="commodity">
		<my-header>
			<div slot="my-header-left" @click="gohome">
				<img src="../assets/img/icons/back_white.png"/>
			</div>
			商品详情
		</my-header>
		<!--悬浮按钮gohome-->
		<router-link to="/" class="gohome"></router-link>
		<!--幻灯片-->
		<mt-swipe :auto="4000">
			<mt-swipe-item v-for="item in goods.banner" :key="item.id"><img :src="item.imgUrl"/></mt-swipe-item>
		</mt-swipe>
		<!--商品详情-->	
		<div class="commo-pro">
			<p>{{goods.title}} <a href="javascript:;">{{goods.shop}}></a></p>
			<span class="price">售价：{{goods.price}}</span>
			<s class="oldprice">原价{{goods.oldPrice}}</s>
			
			<div class="counts">
				<b>购买数量 <span>{{count}}</span></b>
				<div class="mui-numbox">
					<button class="mui-btn mui-btn-numbox-minus" type="button" @click="reduce">-</button>
					<input class="mui-input-numbox" type="number" v-model="count"/>
					<button class="mui-btn mui-btn-numbox-plus" type="button" @click="add">+</button>
				</div>
			</div>
			<p class="commo-infoBody-txt">七天免理由退货</p>
			<p class="commo-infoBody-txt">购物满1000元七折优惠，已享七折优惠</p>
		</div>
		<div class="comment">
			<h3>评论留言</h3>
			<div><a href="javascript:;">居家男孩</a><p>衣服挺厚的，挺喜欢这个款式，给个赞！</p></div>
			<div><a href="javascript:;">香花物语</a><p>衣服挺厚的，挺喜欢这个款式，给个赞！</p></div>
			<button>更多评论  ></button>			
		</div>
		<!--选择颜色与尺寸-->
		<my-pop v-model="parentShow" @abc="next">
			<b>颜色：</b>
			<mt-tabbar v-model="color">
				<mt-tab-item v-for="item in goods.color" :id="item" :key="item.id">
					{{item}}
				</mt-tab-item>
			</mt-tabbar>
			<b>型号：</b>
			<mt-tabbar v-model="size">
				<mt-tab-item v-for="item in goods.size" :id="item" :key="item.id">
					{{item}}
				</mt-tab-item>
			</mt-tabbar>
		</my-pop>
		<!--底部加入购物车-->
		<div class="addgoods">
			<mt-button type="default" size="large" @click="addShopCar">加入购物车</mt-button>
		</div>
		
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				goods:{},
				goodsInfo:'1',
				color:'',
				size:'',
				count:1,
				parentShow:false
			}
		},
		created(){
			this.$http.get("/goodsDetail/:"+this.$route.params.id).then(res=>{
				var obj = res.data.data.find(item=>{
					return item.id == this.$route.params.id;
				})
				this.goods = obj;
//				console.log(this.goods);
				this.color = this.goods.color[0];
				this.size = this.goods.size[0];
			})
		},
		methods:{
			reduce(){
				if(this.count >1 ){
					this.count--;
				}
			},
			add(){
				this.count++;
			},
			addShopCar(){
				this.parentShow = true;
				console.log("添加成功")
			},
			next(){
				var obj = {
					id:this.goods.id,
					title:this.goods.title,
					price:this.goods.price,
					decription:this.goods.decription,
					img:this.goods.banner[0].imgUrl,
					options:[
						{
						color:this.color,
						size:this.size,
						count:this.count
						}
					],
					totalCount:this.count
				}
				//判断颜色选择
				if(!this.color){
					Toast("请选择您需要购买的颜色！");
					return
				}
				//判断尺寸选择
				if(!this.size){
					Toast("请选择您需要购买的尺寸！");
					return
				}
				//判断购物车中有没有这件商品
				var res = this.$store.state.shopCarList.find(item=>{
					return item.id == obj.id;
				})
				if(res){
					res.options.push(obj.options[0]);
					res.totalCount+=obj.options[0].count;
					this.$store.state.shopCarList = this.$store.state.shopCarList.map(item=>{
						if(item.id == obj.id){
							return res;
						}
						return item;
					})
				}else{
					this.$store.commit('changeList',obj)
				}
				Toast("商品已加入购物车!")
				
				this.$router.push({name:'shopCar',params:{id:this.goods.id}}); //跳转到购买人页（舍弃）
					
			},
			gohome(){
				this.$router.push("/")
			}
			
		}
		
	}
</script>

<style scoped lang="less">
		/*悬浮按钮gohome*/
		.gohome{
	position:fixed;
	right: 10px;
	bottom:20% ;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	border: 1px solid #777777;
	text-align: center;
	background:url(../assets/img/icons/home2.png) no-repeat center 0px;
	background-size: 38px;
}
	/*评论*/
	.comment{
		overflow: hidden;
		width:100%;
		background:#fff;
		
		h3{
			font-weight: normal;
		}
		div{
			width: 98%;
			margin: 8px 1%;
			overflow: hidden;
			border-top:1px solid #E0E0E0;
		}
		a{
			display:block;
			width: 80%;
			height: 40px;
			margin-left:2%;
			padding-left: 60px;
			line-height:40px;
			background: url(../assets/img/eva1.png) no-repeat 0 0;
			background-size: 40px;
		}
		p{
			height:30px;
			line-height: 30px;
			margin:0 0 0 10%;
		}
		button{
			display: block;
			width: 150px;
			height:34px;
			line-height: 24px;
			color:#fff;
			font-size: 14px;
			border:none;
			background: #333;
			border-radius: 8px;
			margin: 10px auto;
		}	
	}
	/*购买数量*/
	.counts{
		width: 100%;
		height: 35px;
		position: relative;
		margin: 8px 0;
		b{
			display: block;
			width: 150px;
			height: 35px;
			line-height: 35px;
			font-weight: normal;
			
			span{
				display: block;
				width: 55px;
				height: 35px;
				line-height: 35px;
				text-align: center;
				float: right;
				margin-right: 20px;
			}
		}
	}
	.mui-numbox{
		position: absolute;
		top:0;
		left: 150px;
	}
	
	/*优惠方案*/
	.commo-infoBody-txt{
		padding-left:10%;
		background:url(../assets/img/icons/choce.png) no-repeat 4% 10px;
		font-size: 12px;
	}
	/*底部按钮*/
	.addgoods{
		width: 100%;
		background: #fff;
		position: fixed;
		bottom:0px;
		left: 0;
	}
	.mint-button--default {
	    color: #fff;
	    background-color: #D71E25;
	    -webkit-box-shadow: 0 0 1px #b8bbbf;
	    box-shadow: 0 0 1px #b8bbbf;
	}
	/*颜色*/
	.mint-tabbar{
		margin-top: 4px;
		position: static;
	}
	.mint-tabbar > .mint-tab-item.is-selected{
		color: #D71E25;
	}
	/*幻灯片*/
	.mint-swipe{height:190px}
	.mint-swipe img{width:100%;height: 210px;}
	

	
#commodity{
	background: #F9F9F9;
	overflow: hidden;
	margin: 41px 0 56px 0;
}
.commo-img{
	width: 253px;
	height: 360px;
	margin:0 auto;
	padding: 15px;
	position: relative;
}
.commo-img img{
	position: absolute;
	top: 74px;
	left:40px;
	transform:scale(1.5,1.5) ;
}
.commo-ls{
	width:190px;
	height:10px;
	margin:0 auto;
}
.commo-pro{
	overflow: hidden;
	padding-left: 3%;
	padding-top: 8px;
	padding-bottom: 5px;
	background: #fff;
}
.commo-pro p{
	color:#000;
	width: 96%;
	margin: 10px 0;
	font-size: 14px;
	line-height: 28px;
	font-weight: inherit;
}
.commo-pro p a{
	font-size: 16px;
	display: inline-block;
	float: right;
}
.commo-pro span{
	width: 100px;
	height: 30px;
	line-height: 30px;
	display:inline-block;
	color: #ed1a1a;
	}
.commo-pro s{
	display: inline-block;
	font-size: 16px
}

.commo-info{
	overflow: hidden;
	margin-bottom: 48px;
	border-top:2px solid #f0f0f0;
	margin-top:6px;
	background: #fff;
	width:100%;}
/*商品详情-参数选项卡*/
/*.commo-infoBody{
	overflow: hidden;
	width:100%;
	}
.commo-infoBody p{
	height:30px;
	margin: 10px 0;
	line-height:30px;
}
.commo-infoBody p b{
	color: #000;
	width: 45px;
	float: left;
	font-weight: normal;
	margin-left:3%;
	line-height:28px;
}*/
	/*选项卡*/
	/*.mint-tab-item-label{
		font-size:20px;
	}
	.mint-navbar a{
	    color: #A8A8A8;
	    
	}
	.mint-navbar .mint-tab-item.is-selected {
	    border-bottom: 2px solid #D71E25;
	    color: #D71E25;
	    margin-bottom: -3px;
	}*/
</style>


