import myfooter from "./footer/footer";
import myfooterItem from "./footer/footer-item";
import myheader from "./header"
import mypop from "./pop"
const install = function(Vue){
	Vue.component(myfooter.name,myfooter);
	Vue.component(myfooterItem.name,myfooterItem);
	Vue.component(myheader.name,myheader);
	Vue.component(mypop.name,mypop)
	
}
export default install;


