<template>
	<div id="list">
	<!--头部-->
	<my-header>
			<div slot="my-header-left" @click="goback">
				<img src="../assets/img/icons/back_white.png"/>
			</div>
			{{title}}
		</my-header>
	<!--底部-->
		<myfooters></myfooters>
	
	<!--正文-->
	<router-link class="ListItem" v-for="item in goodsList" :key="item.id" :to="{name:'goodsDetail',params:{id:item.id}}">
		<div class="ListItem-left">
			<img :src="item.imgUrl"/>
		</div>
		<div class="ListItem-right">
			<p>{{item.title}}</p>
			<p>{{item. description}}</p>
			<p>{{item.price}}</p>
		</div>
	</router-link>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				goodsList:[],
				title:"时尚新品"
			}
		},
		created(){
			if(this.$route.params.title){
				this.title = this.$route.params.title;
			}
			this.$http.get("/list/:1").then(res=>{
				
				this.goodsList = res.data.data;
				console.log(this.goodsList);
			})
		},
		methods:{
			goback(){
				this.$router.push("/")
			}
		}
	}
</script>

<style scoped lang="less">
	#list{
		margin-top: 48px;
		margin-bottom: 54px;
	}
	.ListItem{
		width: 100%;
		overflow: hidden;
		margin-bottom: 8px;
		display: flex;
		border-bottom: 1px solid #ddd;
		&-left{
			width: 120px;
			float: left;
			height: 93px;
			text-align: center;
			line-height: 93px;
			img{
				width: 90px;
				height:80px ;
			}
		}
		
		&-right{
			flex: 1;
			
		}
	}
</style>


