<template>
	<div id="ppt">
		<mt-swipe :auto="4000">
			<mt-swipe-item v-for="item in banner" :key="item.id"><img :src="item.imgUrl"/></mt-swipe-item>
		</mt-swipe>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				banner:[]
			}
		},
		created(){
			this.$http.get('/banner').then(res=>{
				this.banner = res.data.data;
			})
		}
	}
</script>

<style>
	#ppt{
		background: #fff;
		}
	.mint-swipe{height:160px; width: 100%;}
	.mint-swipe img{height: 160px; width: 100%;}
</style>




