<template>
	<div id="logon">
		<my-header>
			<div slot="my-header-left" @click="$router.go(-1)">
				<img src="../assets/img/icons/back_white.png"/>
			</div>
			登录
			<div slot="my-header-right" @click="goLogin1">
				注册
			</div>
		</my-header>
		
		<div class="logonInfo">
			<div class="userName">
				<span><img src="../assets/img/icons/reg-user.png" alt="" /></span>
				<input type="text" placeholder="请输入用户名" v-model="userName"/>
			</div>
			<div class="logonPsw">
				<span><img src="../assets/img/icons/reg-psw1.png" alt="" /></span>
				<input type="text" placeholder="请输入密码（6-18位字符）" v-model="userPsw"/>
			</div>
		</div>
		<div class="goreg">
			<span>忘记密码？</span>
		</div>
		<div class="logonBtn">
			<button id="nexreg" @click="logon" >登录</button>
		</div>
		<div class="other">
			<span>其他方式登录</span>
		</div>
		<div class="oPho">
			<img src="../assets/img/icons/loginQQ.png" class="oPholeft"/>
			<img src="../assets/img/icons/loginWeibo.png" alt="" />
			<img src="../assets/img/icons/loginWeiXin.png" class="oPhoright"/>
		</div>
		</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				userName:"",
				userPsw:""
			}
		},
		methods:{
			logon(){
				if(!this.userName){
					Toast("请输入用户名")
				}else if(!this.userPsw){
					Toast("请输入密码")
				}else{
					this.$store.commit('changeName',this.userName);
					this.$router.go(-1);
				}
				
			},
			goLogin1(){
				this.$router.push("/login1")
			}
		}
	}
</script>

<style>
	#logon{
	width: 100%;
	margin-top: 68px;
	background: #fff;
	}
.logonInfo>div{
	position: relative;
	width:80%;
	height: 36px;
	line-height: 36px;
	margin:35px 10%;
	}
.logonInfo span{
	position: absolute;
	bottom:0;
	left:0px;
	display:block;
	width: 36px;
	height: 36px;
	}
.logonInfo span img{
	width: 36px;
	
}
.logonInfo input{
	width: 100%;
	height: 36px;
	line-height: 36px;
	text-indent: 36px;
	font-size: 16px;
	color:#949494;
	border:none;
	outline: none;
	}
.logonInfo .userName input{
	border-bottom: 1px solid #c9c9c9;
	}
.logonInfo .logonPsw input{
	border-bottom: 1px solid #c9c9c9;
	}

.goreg{
	height: 36px;
	line-height: 36px;
	width: 100%;
	color: #949494;
}
.goreg span{
	float: left;
	margin-left: 10%;
	display: inline-block;
	width: 100px;
	height: 30px;
	line-height: 30px;
	text-align: center;
}
.logonBtn{
	width: 100%;
	height: 36px;
	margin-top: 10px;
	}
.logonBtn button{
	width: 80%;
	margin: 10px 10%;
	background: #EE6B70;
	border-radius: 4px;
	font-size: 16px;
	/*letter-spacing:0.2em;
	font-weight: bold;*/
	line-height: 36px;
	color:#fff;
	border: none;
}
.other{
	margin-top: 30px;
	height: 36px;
	line-height: 36px;
	width: 100%;
	color: #949494;
}
.other span{
	float: left;
	margin-left: 10%;
	margin-top: 20px;
	display: inline-block;
	width: 260px;
	height: 30px;
	color: #949494;
	line-height: 30px;
	}
.oPho{
	width: 80%;
	margin: 0 10%;
	padding: 30px 0;
	text-align: center;
}
.oPho img{
	width: 80px;
	height: auto;
	}	
.oPholeft{
	float: left;
}
.oPhoright{
	float: right;
}
</style>


