<template>
	<div id="shoppingCar">
		<!--没有params没有头部-->
		<div class="shoppingCar" v-if="showheader"></div>	
	
		<!--购物车内容-->
		<div class="goodslist" v-for="item in newShopCar" :key="item.id">
			
			<div class="shopLeft">
				<div class="mui-input-row mui-checkbox mui-left">
					<input class="mycheckbox" name="checkbox" v-model="check" :value="item.id" type="checkbox" checked>
				</div>
				<img :src="item.img" alt="" />
			</div>
			
			<div class="shopRight">
				<p class="title">{{item.title}}</p>
				<p v-for="option in item.options" class="option">
					<span>颜色：{{option.color}}</span>
					<span>{{option.size}}</span>
					<span>数量：{{option.count}} 件 </span>
				</p>
				<p class="price"><span>单价:{{item.price}}</span> <router-link to="#" class="goNow">拼单</router-link></p>
				
			</div>
		</div>
		
		<!--头部-->
			<my-header v-if="showheader">
				<div slot="my-header-left" @click="gohome">
					<img src="../assets/img/icons/back_white.png"/>
				</div>
				购物车
			</my-header>
			
		<!--底部购买/判断有没有商品-->
		<div class="addgoods" v-if="isshow">
			<p>总金额：{{total}}</p>
			<mt-button type="danger" size="large" @click="buygoods">立即付款</mt-button>
			<div class="showfooter" v-show="showfooter">
				<!--悬浮按钮gohome-->
				<router-link to="/" class="gohome"></router-link>
			</div>
		</div>
		<!--判断-->
		<div class="unshopCar" v-else>
			<p>目前还没有商品哦！</p>
			<!--底部-->
			<myfooters></myfooters>
		</div>

		<!--添加地址-->
		<my-pop v-model="parentShow" @abc="next">
			<div  class="address">
				<ul>
					<li><span>收货人：</span><input type="text" v-model="shopper" /></li>
					<li><span>收货地址：</span><input type="text" v-model="address" /></li>
					<li><span>联系方式：</span><input type="text" placeholder="" v-model="shopperTel" /></li>
				</ul>
			</div>
		</my-pop>
		
	</div>	
</template>

<script>
	import { Toast } from 'mint-ui'
	import { mapState } from "vuex";
	export default{
		data(){
			return{
				check:[],
				isshow:false,
				parentShow:false,
				shopper:'',
				address:'',
				shopperTel:'',
				newShopCar:[],
				total:0,
				bottomShow:false,
				showheader:false,
				showfooter:false
			}
		},
		created(){
			this.newShopCar = this.shopCarList;
			console.log(this.newShopCar)
			//底部购买/判断有没有商品
			if(this.shopCarList[0]){
				this.isshow = true;
			}
			//没有参数隐藏头部
			if(this.$route.params.id){
				this.showheader = true;
			}
			if(!this.$route.params.id){
				this.showfooter = true;
			}
			this.shopper = this.$store.state.name;
			this.address = this.$store.state.address;
			this.shopperTel = this.$store.state.tel;
		},
		computed:{
			...mapState(['shopCarList']),
			buyList(){
				let buyList = [];
				let shopperInfo = {
					 	shopper:this.shopper,
						address:this.address,
						shopperTel:this.shopperTel
					}
				this.check.forEach(id=>{
					 let obj = this.shopCarList.find(item=>{
					 	item.shopperInfo = shopperInfo;
					 	return item.id == id
					 })
					buyList.push(obj)
				})
				return buyList;
			}
		},
		methods:{
			buygoods(){	//点击购买弹出地址
				if(!this.$store.state.name){
					Toast("请先登录")
					this.$router.push('/logon')
				}else{
					//this.$router.push({name:'address',params:{buylist:this.buyList}}); //跳转到购买人页（舍弃）
					
					this.parentShow = true;
				}
				
				
			},
			next(){	//收货地址点击确定后执行的代码
				this.parentShow = false;
//				将购买的商品添加到数据库
				this.$store.commit('changeByList',this.buyList);
				
//				过滤未购买的商品，提交到state
				this.check.forEach(id=>{
					 this.newShopCar = this.newShopCar.filter(item=>{
					 	
					 	return item.id != id
					 })

				})
				this.$store.commit('update',this.newShopCar);
				
				
				
			},
			gohome(){
//				this.$router.push('-1',)
				this.$router.push({name:'goodsDetail',params:{id:this.$route.params.id}});
			}
		},
		watch:{
			newShopCar(val){
				
				this.check = this.newShopCar.map(item=>{
					return item.id;
				})
			},
			check(val){
				this.total = 0;
				val.forEach(id=>{
					 let obj = this.newShopCar.find(item=>{
						return item.id == id
					 })
					this.total+= obj.price*obj.totalCount
				})
				if(this.check[0]){
					this.isshow = true;
				}
			}
		}
	}
</script>

<style scoped lang="less">
/*悬浮按钮gohome*/
		.gohome{
	position:fixed;
	right: 10px;
	bottom:20% ;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	border: 1px solid #777777;
	text-align: center;
	background:url(../assets/img/icons/home2.png) no-repeat center 0px;
	background-size: 38px;
}
/*底部购买*/
	.addgoods{
		position: fixed;
		left: 0;
		bottom:0;
		z-index: 100;
		width: 100%;
		background: #fff;
		
		p{
			width:100%;
			height:30px;
			line-height: 40px;
			text-align: center;
			border-top: 1px solid #ddd;
		}
	}
/*添加地址*/
.address{
		width: 100%;
		height: 300px;
		background: #fff;
		.public{
			display: inline-block;
			height: 36px;
			line-height: 36px;
		}
		ul{
			margin: 0;
			padding: 20px 0;
		}
		li{
			list-style: none;
			height: 56px;
			width: 100%;
			line-height: 36px;
			padding: 10px 3%;
		}
		span{
			.public;
			width: 30%;
		}
		input{
			.public;
			width: 70%;
		}
	}
	
	.unshopCar{
	margin-top: 150px;
	width: 100%;
	height: 24px;
	line-height: 24px;	
	text-align: center;
}
	
	
	/*拼单按钮*/
	.goNow{
		display: inline-block;
		height: 22px;
		width:48px;
		text-align: center;
		line-height: 22px;
		border-radius: 4px;
		border:1px solid #EDEEEF;
		color: #878C97;
		background: #F6F8FA;
	}
	/*单选按钮*/
	.mui-checkbox { 
		width:36px;
		height: 120px;
	}
	.mui-radio input[type='radio'], .mui-checkbox input[type='checkbox']{
		left: 4px;/*X-28/2*/
		top:47px;
	}
	.mui-radio input[type='radio']:checked:before, .mui-checkbox input[type='checkbox']:checked:before{
		color:#D71E25;
	}
	
	#shoppingCar{
		overflow: hidden;
		
		
	}
	.shoppingCar{margin-top: 40px;}
	.goodslist{
		
		margin-bottom: 8px;
		overflow: hidden;
		background: #Fff;
		padding-right: 8px;
	}
	.shopLeft{
		width: 35%;
		height: 120px;
		float: left;
		position: relative;
		img{
			position: absolute;
			left: 36px;
			top:20px;
			width: 80px;
			height: 80px;
		}
		/*.mycheckbox{
			left:8px;
		}*/
	} 
	.shopRight{
		width: 65%;
		float: left;
		height: 120px;
		position: relative;
		overflow: hidden;
		p{
			height: 24px;
			line-height: 30px;
			span{
				display: inline-block;
				width: 33%;
				float: left;
			}
		}
		.title{
			margin-top: 5px;
			font-size: 16px;
			width: 100%;
			overflow: hidden; /*自动隐藏文字*/
	    	text-overflow: ellipsis;/*文字隐藏后添加省略号*/
	    	white-space: nowrap;/*强制不换行*/
	    	width: 20em;/*不允许出现半汉字截断*/
		}
		.option{
			position: absolute;
			left: 0;
			background: #fff;
			top: 39px;
			width: 100%;
			color: #929292;
		}
		.price {
			margin-top: 34px;
			span{
				color: #f00;
				font-weight: bold;
			}
		}
	}
</style>


