<template>
	<div class="myfooter" >
		<slot></slot>
	</div>
</template>

<script>
	export default{
		name:"my-footer",
		props:["value"]
	}
</script>

<style scoped lang="less">
.myfooter{
	z-index: 1000;
	width: 100%;
	height: 54px;
	border-top:1px solid #888;
	background: #fff;
	display: flex;
	position: fixed;
	left: 0;
	bottom:0;
}
</style>


