<template>
	<div class="myfooter-item" @click="changePage" :class="this.id == this.$parent.value?active:''" >
		<span class="myfooter-item-icon">
			<slot name="myfooter-item-icon"></slot>
		</span>
		<span class="myfooter-item-number">
			<slot name="myfooter-item-number"></slot>
		</span>
		
		<span><slot></slot></span>
	</div>
</template>

<script>
	export default{
		name:"my-footer-item",
		props:['id'],
		data(){
			return{
				active:"footer-item-active"
			}
		},
		methods:{
			changePage(){
				this.$parent.$emit("input",this.id);
				this.$router.push(this.id);
			}
		},
		watch:{
			"$route.path"(abc){
				this.$parent.$emit("input",this.$route.path);
			}
		}
	}
</script>

<style lang="less" scoped>
.myfooter-item{
	flex:1;
	text-align: center;
	font-size: 12px;
	color:#888;
	position: relative;
	&.footer-item-active{
		color:#D71E25;
	}
	span{
			display:block;
		}
	&-icon{
		margin-top: 4px;
		i{
			width: 30px;
			height: 28px;
		}
	}
	&-number{
		position: absolute;
		top:0;
		right: 10px;
		span{
		width: 20px;
		height: 20px;
		line-height: 20px;
		text-align: center;
		border-radius: 50%;
		background: #D71E25;
		color: #fff;
		}
	}
}
</style>


