<template>
	<div class="my-header">
		<div class="my-header-btn" >
			<slot name="my-header-left"></slot>
		</div>
		<h3><slot></slot></h3>
		<div class="my-header-btn">
			<slot name="my-header-right"></slot>
		</div>
		
	</div>
</template>

<script>
	export default{
		name:"my-header"
	}
</script>

<style lang="less" scoped>
.my-header{
	z-index: 500;
	width: 100%;
	position: fixed;
	top:0;
	left: 0;
	height: 40px;
	background: #D71E25;
	display: flex;
	
	h3{
		margin: 0;
		flex: 1;
		text-align: center;
		font-weight: normal;
		color: #fff;
		font-size: 18px;
		line-height: 40px;
		
	}
	
	&-btn{
		width: 70px;
		line-height: 40px;
		text-align: center;
		color: #fff;
		img{
			width: 28px;
			height: 28px;
			margin-top: 6px;
			margin-right:12px;
		}
	}
}
</style>


