<template>
	<div id="user">
	
		<div class="userTop">
			<div class="setting" @click="setting">
				<div class="setbg" v-if="showSet"></div>
				<div class="setbody" v-if="showSet">
					<p @click="signout">退出</p>
					<p>取消</p>
				</div>
				
			</div>
			<img src="../assets/img/icons/user-pho.png"/>
			<div class="login" v-if="isshow">
				<router-link to="/logon">立即登陆</router-link>
			</div>
			<div class="logon" v-else>
				<span>{{this.$store.state.name}}</span>
			</div>
			
		</div>
		<!--底部-->
		<myfooters></myfooters>
		
		<div class="userBody">
			<div class="left">
				<img src="../assets/img/icons/user-purse.png"/>
				<span>钱包</span>
			</div>
			<div class="right">
				<img src="../assets/img/icons/user-card.png"/>
				<span>卡卷</span>
			</div>
		</div>
		<div class="listNav">
			<a href="javacript:;">
				<img src="../assets/img/icons/user-buy.png"/>
				<span>待付款</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-car.png"/>
				<span>待收货</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-tex.png"/>
				<span>待评价</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-oder.png"/>
				<span>订单</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-favorit.png"/>
				<span>关注</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-shop.png"/>
				<span>收藏</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-ask.png"/>
				<span>客服</span>
			</a>
			<a href="javacript:;">
				<img src="../assets/img/icons/user-history.png"/>
				<span>记录</span>
			</a>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				isshow:true,
				showSet:false,
				name:this.$store.state.name
			}
		},
		created(){
			if(this.$store.state.name){
				this.isshow= false;
			}
		},
		methods:{
			setting(){
				this.showSet = !this.showSet;
			},
			signout(){
				this.$store.state.name = ''
				this.$store.state.tel = ''
				this.$store.state.address = ''
				this.$store.state.shopCarList = []
				this.$store.state.byList = []
				this.name = ''
			}
		},
		watch:{
			name(val){
				if(!val){
					this.isshow = true;
				}
			}
		}
	}
</script>

<style scoped lang="less">
	.setting{
		background: url(../assets/img/icons/setting.png);
		position: fixed;
		top:0;
		right: 0;
		width: 48px;
		height: 48px;
		p{
			margin: 0;
			width: 80px;
			height: 30px;
			text-align: center;
			line-height: 30px;
			border-bottom:1px solid #dedede ;
		}
		.setbg{
			position: fixed;
			top: 0;
			left: 0;
			z-index: 10000;
			width: 100%;
			height: 100%;
		}
		.setbody{
			border:1px solid #dedede;
			border-bottom: none;
			position: fixed;
			z-index: 10001;
			top:48px;
			right:8px;
			width: 80px;
			height: 60px;
			background: #Fff;
			color: #333333;
			
		}
		}
#user{
	background: #F9F9F9;
	margin-bottom: 50px;
}
.userTop{
	position: relative;
	width: 100%;
	height: 204px;
	background: #fff;
	box-shadow: 0px 1px 3px #DEDEDE;
}

.userTop img{
	position:absolute;
	top:60px;
	left:42%;
}
.userTop a{
	display: block;
	width: 80px;
	height: 24px;
	line-height: 24px;
	text-align: center;
	position:absolute;
	top:160px;
	left:42%;
	background: #bab7b6;
	color: #fff;
	border-radius: 4px;
}
.userTop span{
	display: block;
	width: 80px;
	height: 24px;
	line-height: 24px;
	text-align: center;
	position:absolute;
	top:160px;
	left:42%;
	color: #333;
	border-radius: 4px;
}
.userBody{
	overflow: hidden;
	padding: 4px 0;
	}
.userBody>div{
	position: relative;
	width: 49.5%;
	height:100px;
	float: left;
	background: #fff;
	text-align: center;
	box-shadow: 0px 1px 3px #DEDEDE;
}
.userBody>div img{
	width: 60px;
	height: auto;
}
.userBody>div span{
	display: block;
	width: 60px;
	height: 24px;
	line-height: 24px;
	margin: 0 auto;
}
.userBody .left{
	margin-right: 1%;
}
.listNav{
	padding: 20px 0;
	width: 100%;
	background: #fff;
	overflow: hidden;
	}
.listNav a{
	margin: 15px 0; 
	float: left;
	display: block;
	width: 25%;
	height: 80px;
	text-align: center;
}
.listNav a img{
	width: 48px;
	height: auto;
}
.listNav a span{
	display: block;
	margin:0 auto;
	font-size: 14px;
	width: 48px;
	height: 20px;
	line-height: 20px;
}

</style>


