<template>
	<div id="login1">
		<my-header>
			<div slot="my-header-left" @click="$router.go(-1)">
				<img src="../assets/img/icons/back_white.png"/>
			</div>
			注册
		</my-header>
		
		<div class="regInfo">
			<div class="reg-phone">
				<span><img src="../assets/img/icons/reg-phone.png" alt="" /></span>
				<input type="text" placeholder="请输入手机号码" v-model="tel"/>
			</div>
			<div class="reg-email">
				<span>获取手机验证码</span>
				<input type="text" placeholder="六位验证码" v-model="ify"/>
				
			</div>
		</div>
		<div class="regsubBtn">
			<button class="nexreg" @click="nexreg">下一步</button>
		</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				tel:"",
				ify:""
			}
		},
		methods:{
			nexreg(){
				if(!this.tel){
					Toast("请输入手机号")
				}else if(!this.ify){
					Toast("请输入验证码")
				}else{
					this.$store.commit('changeTel',this.tel)
					this.$router.push('/login')
				}
				
			}
		}
	}
</script>

<style >

.regtop{
	height:74px;
	width: 100%;
	background: #fff;
	text-align: center;
	line-height: 74px;
	font-size: 20px;
	box-shadow: 1px 1px 0px 1px #f7f7f7;
}
.regtop span{
	display: block;
	width: 60px;
	height: 74px;
	float: left;
	margin-left: 2%;
	
	background: url(../assets/img/icons/before.png) no-repeat center;
}
.regInfo{
	margin-top: 80px;
}
.regInfo>div{
	position: relative;
	width:80%;
	height: 36px;
	line-height: 36px;
	margin: 40px 10%;
	}
.regInfo span{
	position: absolute;
	bottom:0;
	left:0px;
	display:block;
	width: 36px;
	height: 36px;
	}
.regInfo span img{
	width: 36px;
	
}
.regInfo input{
	width: 100%;
	height: 36px;
	text-indent: 2em;
	line-height: 36px;
	font-size: 16px;
	color:#949494;
	border:none;
	outline: none;
	}
.regInfo .reg-phone input,.regInfo .reg-psw input{
	border-bottom: 1px solid #c9c9c9;
	}
.reg-email input{
	width: 50%;
	height: 36px;
	line-height: 36px;
	text-indent: .5em;
	margin-left: 50%;
	border-bottom: 1px solid #c9c9c9;
	}
.reg-email span{
	background: #E5E5E5;
	color:#fff;
	text-align: center;
	border-radius:6px;
	width: 48%;
	}
.reg-footer{
	clear: all;
	width: 100%;
	height: 40px;
	position: relative;
}
.regsubBtn{
	width: 100%;
	height: 36px;
	margin-top: 80px;
	}
.regsubBtn button{
	width: 80%;
	margin: 10px 10%;
	background: #EE6B70;
	border-radius: 4px;
	font-size: 16px;
	/*letter-spacing:0.2em;
	font-weight: bold;*/
	line-height: 36px;
	color:#fff;
	border: none;
}
</style>


