<template>
	<div id="reg2">
		<my-header>
			<span @click="$router.go(-1)" slot="my-header-left" >
				<img src="../assets/img/icons/back_white.png"/>
			</span>
			登录
		</my-header>
		
		<div class="regInfo">
			<div class="reg-phone">
				<span><img src="../assets/img/icons/reg-user.png" alt="" /></span>
				<input type="text" placeholder="请输入用户名" v-model="userName"/>
			</div>
			<div class="reg-psw">
				<span><img src="../assets/img/icons/reg-psw1.png" alt="" /></span>
				<input type="password" placeholder="请输入密码（6-18位字符）" v-model="userPsd"/>
			</div>
			<div class="reg-psw">
				<span><img src="../assets/img/icons/reg-psw2.png" /></span>
				<input type="password" placeholder="确认密码" v-model="ifyPsd"/>
				<b v-show="isshow" style="font-weight: normal;margin-left:50px ;font-size: 14px;color: #f60;">
					密码输入有误
				</b>
			</div>
		</div>
		<div class="regsubBtn">
			<button @click="logon">注册</button>
		</div>
	</div>
</template>

<script >
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return{
				userName:'',
				userPsd:'',
				ifyPsd:'',
				isshow:false
			}
		},
		methods:{
			logon(){
				if(!this.userName){
					Toast("请输入用户名")
				}else if(!this.userPsd){
					Toast("请输入密码")
				}else if(!this.ifyPsd){
					Toast("请确认密码")
				}
				else if(this.userPsd != this.ifyPsd){
					this.isshow = true;
				}else{
					console.log("成功")
					this.isshow = false;
					this.$store.commit('changeName',this.userName);
					this.$router.push("/my")
				}
			}
		}
	}
</script>

<style scoped>
	#reg{
	width: 100%;
	}
.regtop{
	height:74px;
	width: 100%;
	background: #fff;
	text-align: center;
	line-height: 74px;
	font-size: 20px;
	box-shadow: 1px 1px 0px 1px #f7f7f7;
}
.regtop span{
	display: block;
	width: 60px;
	height: 74px;
	float: left;
	margin-left: 2%;
	
	background: url(../assets/img/icons/before.png) no-repeat center;
}
.regInfo{
	margin-top: 80px;
}
.regInfo>div{
	position: relative;
	width:80%;
	height: 36px;
	line-height: 36px;
	margin: 40px 10%;
	}
.regInfo span{
	position: absolute;
	bottom:0;
	left:0px;
	display:block;
	width: 36px;
	height: 36px;
	}
.regInfo span img{
	width: 36px;
	
}
.regInfo input{
	width: 100%;
	height: 36px;
	text-indent: 36px;
	line-height: 36px;
	font-size: 16px;
	color:#949494;
	border:none;
	outline: none;
	}
.regInfo .reg-phone input,.regInfo .reg-psw input{
	border-bottom: 1px solid #c9c9c9;
	}
.reg-email input{
	width: 50%;
	height: 36px;
	line-height: 36px;
	margin-left: 50%;
	border-bottom: 1px solid #c9c9c9;
	}
.reg-email span{
	background: #E5E5E5;
	color:#fff;
	text-align: center;
	border-radius:6px;
	width: 48%;
	}
.reg-footer{
	clear: all;
	width: 100%;
	height: 40px;
	position: relative;
}
.regsubBtn{
	width: 100%;
	height: 36px;
	margin-top: 80px;
	}
.regsubBtn button{
	width: 80%;
	margin: 10px 10%;
	background: #EE6B70;
	border-radius: 4px;
	font-size: 16px;
	/*letter-spacing:0.2em;
	font-weight: bold;*/
	line-height: 36px;
	color:#fff;
	border: none;
}
</style>


